import {
    getAllGradeMsg,
    getOtherGrade,
    getAllRoom,
    addGrade,
    editGrade,
    delGrade
} from '../../service/questManger'
import {
    getAllSubject
} from '@/service/questionsManger'

function getAll() {
    return Promise.all([getAllGradeMsg(), getOtherGrade()])
}

export default {
    namespace:'grade',
    state:{
        allGradeMsg: [],
        allSubject: [],
        allRoom: []
    },
    effects: {
        *delGrade({payload}, {put, call}) {
            yield call(delGrade, payload);
            yield put({
                type: 'getAllMsg'
            })
        },
        *addGrade({ payload }, {put, call}) {
            yield call(addGrade, payload);
            yield put({
                type: 'getAllMsg'
            })
        },
        *editGrade({ payload }, {put, call}) {
            yield call(editGrade, payload);
            yield put({
                type: 'getAllMsg'
            })
        },
        *getAllMsg(_, {put, call}) {
            let allRes = yield call(getAll);
            let allSubject = yield call(getAllSubject);
            let allRoom = yield call(getAllRoom);
            yield put({
                type: 'saveGradeMsg',
                payload: allRes[0].data.concat(allRes[1].data)
            })
            yield put({
                type: 'saveSubject',
                payload: allSubject.data
            })
            yield put({
                type: 'saveRoom',
                payload: allRoom.data
            })
        }
    },
    reducers: {
        saveGradeMsg(state, { payload }) {
            return {
                ...state,
                allGradeMsg: payload
            }
        },
        saveSubject(state, { payload }) {
            return {
                ...state,
                allSubject: payload
            }
        },
        saveRoom(state, { payload }) {
            return {
                ...state,
                allRoom: payload
            }
        }
    }
}